
import React, { Component } from 'react';
import { Row, Checkbox, Button } from 'antd';
import { action, toJS } from 'mobx';
import { inject, observer } from 'mobx-react';


@inject('store')
@observer
class App extends Component {
    state = {
        releaseBugs: false
    };
    render() {
        if (this.state.releaseBugs) {
            throw new Error("!");
        }
        return (
            <div>
                <Button
                    onClick={() => {
                        this.setState({
                            releaseBugs: true
                        });
                    }}
                >
                    
                </Button>
            </div>
        );
    }
}

export default App;


