    handleChange = (name, value, e) => {
        switch (name) {
            case "province":
                this.setState({
                    province: value,
                    cities: this.getCity(value),
                    city: this.getCity(value)[0],
                    counties: this.getCounty(value, this.getCity(value)[0])
                });
                break;
            case "city":
                this.setState({
                    city: value,
                    counties: this.getCounty(this.state.province, value),
                    county: this.getCounty(this.state.province, value)[0]
                });
                break;
            case "county":
                this.setState({
                    county: value
                });
                break;
            default:
                alert("child handleChange error")
        }

    }

    render() {
        let id = 0
        return (
            <div className="col-md-9">
                <Select
                    style={{ width: 120 }}
                    className="select"
                    onChange={(value, e) => {
                        this.handleChange("province", value, e)
                    }}>
                    {this.state.provinces.map(province => (
                        <Option value={province} key={id++}>{province}</Option>
                    ))}
                </Select>
                <Select
                    style={{ width: 120 }}
                    className="select"
                    onChange={(value, e) => {
                        this.handleChange("city", value, e)
                    }}>
                    {this.state.cities.map(city => (
                        <Option value={city} key={id++}>{city}</Option>
                    ))}
                </Select>
                <Select
                    style={{ width: 120 }}
                    className="select"
                    onChange={(value, e) => {
                        this.handleChange("county", value, e)
                    }}>
                    {this.state.counties.map(county => (
                        <Option value={county} key={id++}>{county}</Option>
                    ))}
                </Select>
            </div >
        )
    }
